.content{ width: 100%; }
.content .container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 80%;
    margin: auto ;
    margin-top: 120px;
}
.content .container .element{ width: 20%; }
.content .element .inner{
    position: relative;
    width: 90%;
    height: fit-content;
    overflow: hidden;
    margin-bottom: 50px;
    margin: auto;
}
.content .element .inner img{
        width: 100%;
        height: 300px;
        margin-bottom: 30px !important;
}
.content .element .inner:hover img{
    transition: 1s;
    transform: scale(1.1);
}
.content .element .image{ overflow: hidden; }
.content .element .inner .upper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: fit-content;
    text-align: center;
    transition:all 1s;
    animation: ani 1s ease-in-out;
     color: white; 
     display: flex;
     justify-content: space-between;
}
.content .element .inner .upper p{
    background-color: rgba(0, 0, 0, 0.801);
    padding: 1px 4px 4px 4px;
    border-radius: 5px;
    margin: 10px;
}
.content .inner .upper p .icon{ color: var(--blue); }
.content .inner .text{
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: fit-content;
    text-align: center;
    display: none;
    transition:all 1s;
    animation: animate 1s ease-in-out;
     background-color: rgba(0, 0, 0, 0.777);
     color: white;
}
.content .element .inner:hover .text{ display: block; }
@keyframes animate{
    0%  { opacity: 0; }
    100%{ opacity: 1; }
}
@media (max-width:992px){
.content .element{
      width: 50% !important;
      margin: auto ;
}
.content .element .inner img{ height: 500px; }
}
@media (max-width:768px){
.content .element{
        width: 100% !important;
        margin: auto !important;
}
.content .element .image img{ height: fit-content; }
.content .container{
        flex-direction: column;
        justify-content: center;
}
}