@import url('https://fonts.googleapis.com/css2?family=Hubballi&family=Josefin+Sans:wght@100;200;300;500;600;700&family=Kanit:wght@100;200;300;400;500;700;800;900&family=Montserrat:wght@100;200;300;400;500;600&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
:root{ --blue: #e50914; }
.home{
    background-image: url('./home-cover.jpg');
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    margin-top: 0;
    position: relative;
    display: flex;
    justify-content: center;
}
.home .container{
    background-image: linear-gradient(120deg,black ,rgba(0, 0, 0, 0.862) ,rgba(0, 0, 0, 0.84) ,rgba(219, 204, 204, 0.167),rgba(0, 0, 0, 0.641),rgba(0, 0, 0, 0.767),rgba(0, 0, 0, 0.86));
    width: 100%;
    height: 100vh;
    color: white;
    font-family: Kanit;
    padding: 1px 40px;
    box-sizing: border-box;
}
.home .container .text{
    width: 50%;
    line-height: 1.2;
    margin-top: 250px;
    text-align: left;
}
.home .text h1{
    font-size: 50px;
    letter-spacing: 3px;
}
.home .text span{
    color: gray;
    line-height: 2;
}
.home .text span .production{
    background-color: white;
    color: var(--blue);
    padding:1px 2px;
    border-radius: 2px;
    margin-left: 5px;
    font-weight: bold;
}
.home .text p{
    line-height: 1.5;
  letter-spacing: 1px;
}
@media (max-width:992px){
.home .container .text{ width: 70% !important; }
}
@media (max-width:768px){
.home .container .text{
    width: 100% !important;
    text-align: center;
    margin: auto;
    margin-top: 200px;
}
.home .text h1{ font-size: 35px; }
.home .container .text p{
  width: 100% !important;
  font-size: 14px;
}
}
