 header{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    padding: 1px 40px;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 11;
  }
 header .name p{
    font-size: 35px;
    letter-spacing: 3px;
    width: 50%;
    color: white;
  }
 header .name p::first-letter{
    background-color: var(--blue);
    padding: 0px 5px 0 5px;
    text-align: center;
    border-radius: 3px;
  }
 header .name p .span{ color: var(--blue); }
 header  form{
    width: 50%;
    text-align: end;
    word-spacing: -3px;
}
header form input{
    border: none;
    padding: 15px 20px;
    font-size: 15px;
} 
header form input[type='text']{
    width: 60%;
    outline: none;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    margin-right: 0;
}
header form input[type='submit']{
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    background-color: var(--blue);
    color: white;
    width: 15%;
}
@media (max-width:992px){
header .name{
        width: 30%;
        text-align: center;
}
header form{
        width: 70% !important;
        padding: 0;
        display: flex;
        justify-content: end;
}
header form input[type='text']{ width: 100% !important; }
header form input[type='submit']{ width: 50% !important; }
}
@media (max-width:768px){
header{ flex-direction: column; }
header .name{
        width: 100%;
        display: flex;
        justify-content: center;
}
header form{
      width: 100% !important;
      padding: 0;
      display: flex;
      justify-content: center;
}
}